(() => {
  const works = document.querySelector(`.works__swiper-container`);
  const partners = document.querySelector(`.partners__swiper-container`);
  const reviews = document.querySelector(`.reviews__swiper-container`);

  if (works) {
    let worksSlider = new Swiper(works, {
      slidesPerView: 1,
      spaceBetween: 30,
      a11y: false,
      loop: false,
      initialSlide: 1,
      autoHeight: true,
      navigation: {
        nextEl: `.works__swiper-button--next`,
        prevEl: `.works__swiper-button--prev`,
      },
      breakpoints: {
        1440: {
          slidesPerView: 2,
          centeredSlides: false
        }
      }
    });
  }

  if (partners) {
    let partnersSlider = new Swiper(partners, {
      slidesPerView: 1,
      spaceBetween: 8,
      a11y: false,
      loop: false,
      autoHeight: true,
      navigation: {
        nextEl: `.partners__swiper-button--next`,
        prevEl: `.partners__swiper-button--prev`,
      },
      breakpoints: {
        500: {
          slidesPerView: 2,
        },
        700: {
          slidesPerView: 3,
        },
        900: {
          slidesPerView: 4,
        },
        1100: {
          slidesPerView: 5,
        },
        1280: {
          slidesPerView: 6,
        }
      }
    });
  }

  if (reviews) {
    let reviewsSlider = new Swiper(reviews, {
      slidesPerView: 1,
      spaceBetween: 40,
      a11y: false,
      loop: false,
      initialSlide: 1,
      autoHeight: true,
      navigation: {
        nextEl: `.reviews__swiper-button--next`,
        prevEl: `.reviews__swiper-button--prev`,
      }
    });
  }
})();
